import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue";
import otherLogin from "../../components/home/other-login.vue";
export default {
	name: "login",
	components: {
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status: false, //false代表账号密码登录，true代表手机验证码登录
			username: "",
			password: "",
			phone: "",
			checknum: "",
			loading: false,
			disabled: true,
			codetime: 0,
		}
	},
	watch:{
		username(){
			this.onBtnChange();
		},
		password(){
			this.onBtnChange();
		},
		phone(){
			this.onBtnChange();
		},
		checknum(){
			this.onBtnChange();
		}
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		// 验证手机号
		isPhone(){
			let mPattern = /^1[34578]\d{9}$/; 
			return mPattern.test(this.phone);
		},
		// 改变按钮状态
		onBtnChange(){
			if((this.username && this.password) || (this.phone && this.checknum)){
				this.disabled = false;
				return;
			}
			this.disabled = true;
		},
		// 初始化表单
		initInput(){
			this.username='';
			this.password='';
			this.phone='';
			this.checknum='';
		},
		// 切换登录类型
		changeStatus(){
			this.initInput();
			this.status = !this.status;
		},
		async getCheckNum() {
			if(this.codetime>0){
				uni.showToast({
					title: '请稍后再试',
					icon: "none"
				});
				return;
			}
			// 验证码登录
			// 验证手机号合法性
			if(!this.isPhone()){
				uni.showToast({
					title: '请输入正确的手机号',
					icon: "none"
				});
				return;
			}
			// 请求服务器，发送验证码
			let [err,res] = await this.$http.post('/user/sendcode',{
				phone: this.phone
			});
			// 请求失败处理
			this.$http.errorCheck(err,res)
			if (res.data.errorCode === 30001) return;
			// 发送成功，开启倒计时
			this.codetime = 60;
			let timer = setInterval(() =>{
				this.codetime--;
				if(this.codetime<1){
					clearInterval(timer);
					this.codetime = 0;
				}
			},1000);
			
		},
		submit(){
			// 账号密码登录
			if(!this.status){
				return this.User.login({
					url: '/user/login',
					data: {
						username: this.username,
						password: this.password
					}
				})
			}
			// 验证码登录
			// 验证手机号合法性
			if(!this.isPhone()){
				uni.showToast({
					title: '请输入正确的手机号',
					icon: "none"
				});
				return;
			}
			
			return this.User.login({
				url: '/user/phonelogin',
				data: {
					phone: this.phone,
					code: this.checknum
				}
			})
		}
	}
}
